.at-badge {
  position: relative;
  display: inline-block;
  font-size: 0;
  vertical-align: middle;
}

.at-badge__dot {
  position: absolute;
  right: -0.128rem;
  top: -0.128rem;
  width: 0.42667rem;
  height: 0.42667rem;
  border-radius: 50%;
  overflow: hidden;
  background: #FF4949;
  box-shadow: 0 0.08533rem 0.17067rem 0 rgba(233, 59, 61, 0.2);
}

.at-badge__num {
  font-size: 0.42667rem;
  color: #fff;
  height: 0.59733rem;
  line-height: 0.59733rem;
  position: absolute;
  right: -6PX;
  top: -0.29867rem;
  -webkit-transform: translate(50%, 0);
          transform: translate(50%, 0);
  padding: 0 0.21333rem;
  border-radius: 0.29867rem 0.29867rem 0.29867rem 0;
  overflow: hidden;
  background: #FF4949;
  box-shadow: 0 0.08533rem 0.17067rem 0 rgba(233, 59, 61, 0.2);
}

